h1 {
	color: #426696;
	font-weight: 600;
	font-size: 3rem;
	opacity: 0.8;
}

h2,
p {
	color: #658ec6;
	font-weight: 500;
	opacity: 0.8;
}

h3 {
	color: #426696;
	font-weight: 600;
	opacity: 0.8;
}

main {
	font-family: 'Poppins', sans-serif;
	/*     min-height: 100vh; */
	/*     background: linear-gradient(to right top, #65dfc9, #6cdbeb); */
	display: flex;
	align-items: center;
	justify-content: center;
}

.glass {
	background: white;
	/*     min-height: 80vh; */
	width: 80%;
	background: linear-gradient(
		to right bottom,
		rgba(255, 255, 255, 0.5),
		rgba(255, 255, 255, 0.2)
	);
	border-radius: 2rem;
	z-index: 1;
	backdrop-filter: blur(2rem);
	display: flex;
}
.glass-score{
	background: white;
	/*     min-height: 80vh; */
	background: linear-gradient(
		to right bottom,
		rgba(255, 255, 255, 0.5),
		rgba(255, 255, 255, 0.2)
	);
	border-radius: 2rem;
	z-index: 1;
	backdrop-filter: blur(2rem);
	display: flex;
}

.circle1,
.circle2 {
	background: white;
	background: linear-gradient(
		to right bottom,
		rgba(255, 255, 255, 0.8),
		rgba(255, 255, 255, 0.3)
	);
	height: 20rem;
	width: 20rem;
	position: absolute;
	border-radius: 50%;
}

.circle1 {
	top: 10%;
	right: 10%;
}

.circle2 {
	bottom: 5%;
	left: 10%;
}

.dashboard {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
	text-align: center;
	background: linear-gradient(
		to right bottom,
		rgba(255, 255, 255, 0.7),
		rgba(255, 255, 255, 0.3)
	);
	border-radius: 2rem;
}

.link {
	display: flex;
	margin: 0.5rem 0rem;
	padding: 0.5rem 1rem;
	align-items: center;
	transition: all 0.3s ease 0s;
	&:hover {
		filter: brightness(0) invert(1);
	}
	img {
		width: 50px;
		height: 50px;
	}
}

.link h2 {
	padding: 0rem 1rem;
}

.user {
	width: 100%;
	img {
		width: 80px;
		height: 80px;
	}
}

.games {
	flex: 2;
}

.pro {
	background: linear-gradient(to right top, #65dfc9, #6cdbeb);
	border-radius: 2rem;
	color: white;
	padding: 1rem;
	position: relative;
}

.pro img {
	position: absolute;
	top: -10%;
	right: 10%;
}

.pro h2 {
	width: 40%;
	color: white;
	font-weight: 600;
}

/* GAMES */

.status {
	margin-bottom: 0.5rem;
}

.status input {
	background: linear-gradient(
		to right bottom,
		rgba(255, 255, 255, 0.7),
		rgba(255, 255, 255, 0.3)
	);
	border: none;
	width: 50%;
	padding: 0.5rem;
	border-radius: 2rem;
}

.games {
	margin: 2rem;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
}

.card {
	display: flex;
	background: linear-gradient(
		to left top,
		rgba(255, 255, 255, 0.8),
		rgba(255, 255, 255, 0.5)
	);
	border-radius: 1rem;
	margin: 1rem 0rem;
	padding: 1rem;
	box-shadow: 6px 6px 20px rgba(122, 122, 122, 0.212);
	justify-content: space-between;
}

.progress {
	background: linear-gradient(to right top, #65dfc9, #6cdbeb);
	width: 100%;
	height: 25%;
	border-radius: 1rem;
	position: relative;
	overflow: hidden;
}

.progress::after {
	content: '';
	width: 100%;
	height: 100%;
	background: rgb(236, 236, 236);
	position: absolute;
	left: 60%;
}

.card-info {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	p {
		font-family: 'Marck Script';
		font-weight: 400;
		font-size: 2.5em;
	}
}

.percentage {
	font-weight: bold;
	background: linear-gradient(to right top, #65dfc9, #6cdbeb);
	/*     -webkit-background-clip: text; */
	-webkit-text-fill-color: transparent;
}
