//====================================================================================================
@import '/src/SCSS/index.scss';
*a {
	cursor: pointer;
}

.header {
	position: fixed;
	right: 0;
	z-index: 10;
	/*     width: 100vw; */
	/* .header__container */
	&__container {
	}
	/* .header__body */
	&__body {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 15px 20px 15px 0px;
		@media (max-width: $mobile) {
			margin: 15px 0px 15px 0px;
			justify-content: center;
		}
	}
	/* .header__logo */
	&__logo {
		@media (max-width: $mobile) {
			img {
				width: rem(90);
				height: auto;
				object-fit: contain;
			}
		}
	}
	/* .header__menu */
	&__menu {
		z-index: 4;
		display: flex;
		align-items: center;
	}
	/* .header__button */
	&__button {
		font-family: 'Marck Script';
		display: inline-block;
		margin: 0px 10px 0px 10px;
		position: relative;
		padding: 30px 20px 30px 20px;
		width: 200px;
		border-radius: 2px;
		font-size: 2rem;
		line-height: 18px;
		text-align: center;
		color: #fff;
		cursor: pointer;
		@media (max-width: $tablet) {
			font-size: 1.5rem;
			margin: 10px 0px 0px 0px;
		}
		@media (max-width: $mobile) {
		}
		&::before,
		&::after {
			content: '';
			border: 3px solid #fff;
			position: absolute;
			width: calc(100% - 6px);
			height: calc(100% - 6px);
			left: 0;
			bottom: 0;
			transition: transform 0.3s ease;
		}
		&:hover::after {
			transform: translate(-5px, -5px);
		}
		&:hover::before {
			transform: translate(5px, 5px);
		}
	}
}

.menu {
	/* .menu__icon */
	&__icon {
	}
	/* .menu__body */
	&__body {
	}
	/* .menu__list */
	&__list {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 32px;
		@media (max-width: $mobile) {
			gap: 15px;
		}
	}
	/* .menu__item */
	&__item {
		font-family: 'Press Start 2P';
		font-weight: 400;
		font-size: 18px;
		line-height: 18px;
		text-align: center;
		align-items: center;
		color: #fff;
		display: flex;
		@media (max-width: $mobile) {
			font-size: 13px;
		}
	}
	/* .menu__link */
	&__link {
		@media (any-hover: hover) {
			&:hover {
				text-decoration: underline;
			}
		}
	}
	/* .menu__link */
	&__picture {
		&:not(:last-child) {
			margin: 0px 5px 0px 0px;
		}
		transition: all 0.3s ease 0s;
		&:hover {
			filter: brightness(0) invert(1);
		}
		img {
			width: 45px;
			height: 45px;
			@media (max-width: $mobile) {
				width: 35px;
				height: 35px;
			}
		}
	}
}

.icon-menu {
