@import '/src/SCSS/index.scss';
$offwhite: #fff;
$darkblue: rgba(154, 74, 233, 0.708);
.main {
	&__fullscreen-body {
		height: 100%;
		background: rgb(174, 0, 255);
		background: linear-gradient(
			176deg,
			rgb(80, 7, 117) 0%,
			rgb(46, 209, 221) 100%
		);
	}
	&__fullscreen-wrapper {
		min-height: 100vh;
		display: flex;
		flex-direction: column;
		z-index: 3;
	}
	&__fullscreen {
		min-height: 100vh;
		z-index: 3;
		flex-wrap: wrap;
		display: flex;
		align-items: center;
		padding: 20px 0;
		justify-content: center;
		flex-direction: column;
	}
	&__title {
		font-family: 'Press Start 2P';
		font-weight: 400;
		margin: 0px 0px 0px 0px;
		text-align: center;
		font-size: 4em;
	}
}

.about {
	&__rules {
		font-family: 'Press Start 2P';
		font-weight: 400;
		font-size: 5em;
		color: #ffff;
	}
	&__text {
		font-family: 'Marck Script';
		text-indent: 1em;
		margin: 20px 0px 0px 0px;
		font-size: 3em;
		color: #ffff;
	}
}

.modal {
	&__content {
		font-size: 5em;
		display: flex;
		flex-direction: column;
		color: #fff;
		z-index: 4;
		font-family: 'Marck Script';
		@media (max-width: $mobile) {
			font-size: 3em;
			div {
				margin: 10px 0px 10px 0px;
			}
		}
	}
}

.score {
	&__title {
		font-size: 5em;
	}
	&__box {
		margin: 5px 0px 0px 0px;
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		justify-content: center;
		color: #fff;
		&:nth-child(even) {
			background: linear-gradient(
				to left top,
				rgba(255, 255, 255, 0.8),
				rgba(255, 255, 255, 0.5)
			);
			border-radius: 1rem;
			box-shadow: 6px 6px 20px rgba(122, 122, 122, 0.212);
			color: #658ec6;
		}
	}
	&__person {
		margin: 0px 10px 0px 0px;
		font-size: 2.6em;
		color: inherit;
		input {
			background: transparent;
			max-width: 150px;
			padding: 0px 0px 0px 5px;
			color: inherit;
		}
	}
	&__inpute {
		display: flex;
		gap: 5px;
		input {
			background: transparent;
		}
	}
	&__number {
		display: block;
		max-width: 70px;
		padding: 0px 0px 0px 5px;
		font-size: 3.5em;
		color: inherit;
		white-space: nowrap;
	}
	&__icon {
		font-size: 3em;

		cursor: pointer;
		padding: 0px 10px;
		transition: all 0.3s ease 0s;
		color: #658ec6;
		@media (any-hover: hover) {
			&:hover {
				color: #9226daa1;
			}
		}
	}
	&__bonuses {
		display: flex;
		padding: 0px 0px 0px 5px;

		white-space: nowrap;
	}
	&__bonus {
		cursor: pointer;
		img {
			width: 40px;
			height: 40px;
		}
		&:not(:last-child) {
			margin: 0px 10px 0px 0px;
		}
	}
	&__cleare-local {
		display: inline-block;
	}
	&__cleare-options {
		position: absolute;
		right: 20px;
		bottom: 20px;
	}
}

.bonus-disable {
	filter: saturate(5%);
	scale: (0.7);
}

/* ------------common------------------ */

.striped-text {
	padding: 20px 0px 0px 0px;
	font-family: 'Press Start 2P';
	font-weight: 400;
	font-size: 4em;
	color: rgb(104, 206, 250);
	text-transform: uppercase;
	text-shadow: -4px -4px 0px #d2ead7;
	background: repeating-linear-gradient(
		45deg,
		#57b8f0,
		#08e4f8 1px,
		rgb(231, 92, 246) 2px,
		white 3px
	);
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	display: table;
	margin: 0px 0px 10px 0px;
	@media (max-width: $mobile) {
		padding: 20px 0px 0px 0px;
		font-size: 3em;
	}
}

.shrink-border {
	margin: 5px;
	font-family: 'Marck Script';
	font-size: 2rem;
	line-height: 22px;
	text-align: center;

	cursor: pointer;
	position: relative;
	padding: 15px 10px 15px 10px;
	background-color: transparent;
	color: darken($darkblue, 10%);
	display: flex;
	align-items: center;
	transition: all 0.3s ease 0s;
	&:hover {
		background-color: transparent;
		box-shadow: none;
		color: $offwhite;
	}
	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 25px;
		border: 3px solid darken($darkblue, 10%);
		transition: opacity 0.3s, border 0.3s;
	}
	&:hover::before {
		opacity: 0;
	}
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 25px;
		background-color: transparent;
		border: 3px solid $offwhite;
		opacity: 0;
		z-index: -1;
		transform: scaleX(1.1) scaleY(1.3);
		transition: transform 0.3s, opacity 0.3s;
	}
	&:hover::after {
		opacity: 1;
		transform: scaleX(1) scaleY(1);
	}
}
.button-answer {
	display: flex;
	justify-content: center;
}
.button-right {
	transition: all 0.3s ease 0s;
	&:hover {
		filter: brightness(0) invert(1);
	}
	img {
		width: 40px;
		height: 40px;
	}
}
.button-wrong {
	transition: all 0.3s ease 0s;
	&:hover {
		filter: brightness(0) invert(1);
	}
	img {
		width: 40px;
		height: 40px;
	}
}
/* ------------common------------------ */
